import { Component, OnInit, Input, Output, EventEmitter, SimpleChanges, Injector } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { MatDialog } from '@angular/material/dialog';

import { UserService } from 'src/app/api/controllers/User';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { VehicleService } from 'src/app/api/controllers/Vehicle';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { ModalService } from 'src/app/shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import { PetService } from '../../../../api/controllers/Pet';

@Component({
  selector: 'app-approved-user-details',
  templateUrl: './approved-user-details.component.html',
  styleUrls: ['./approved-user-details.component.scss']
})
export class ApprovedUserDetailsComponent implements OnInit {
  
  
  @Input() selectedTab:number;

  user:any;

  isSeperatePage:boolean = false;

  approvedUsersData:any;

  isUserDataLoaded:boolean = false;

  apartmentBlockUnitId:any;
  unitListData:Array<any>=[];
  unitNameList:Array<any>=[];
  petDataList:Array<any>=[];
  petTypeList:Array<any>=[];
  familyDataList:Array<any>=[];
  userUnitId:any;

  modalService:any;

  unit:any;
  vehicle:any;

  vehicleTypeData:any;

  @Output() public outputParams = new EventEmitter();

  constructor(
    private route: ActivatedRoute,
    private injector: Injector,
    public dialog: MatDialog,
    private userService:UserService,
    private apartmentService:ApartmentService,
    private vehicleService: VehicleService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService,
    private petService:PetService
  ) { 
    this.modalService = this.injector.get(ModalService);
  }

  isTabSelected(){
  	return this.selectedTab != -1 ? 'show' : '';
  }

  isNotSlide() {
    return this.isSeperatePage ? 'full' : '';
  }

  getRoleName(role){
    if(role === undefined || role.length == 0){
      return '';
    }
    else {
      this.user.roleName = role[0].roleName;
      return role[0].roleName;
    }
  }

  getunitStatus(data){
    return data ? 'Yes' : 'No';
  }

  getUnitName(id){

    var data = _.filter( this.unitNameList, function( item ) {
      
      if(item.apartmentBlockUnitId === id){
        return item;
      }

    });


    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].apartmentBlockUnitNumber;
    }

  }
  showUnitDetails(id){
    let petListParams = {
      apartmentBlockUnitId: parseInt(id)
    }

    this.petService.getAllPetsByApartmentBlockUnitId(petListParams).subscribe((res:any) => {

      if(res.length != 0){
        this.petDataList = res.filter(item => { return item.pet.isActive });
      }
      else {
      }
    })


  }

  
  getPetType(id){

  	var data = _.filter( this.petTypeList, function( item ) {
      
      if(item.lookupValueId === id){
      	return item;
      }

    });


    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].lookupValueName;
    }

  }
  getVehicleType(id){

    _.each(this.vehicleTypeData, (item, index) => {

      if(item.lookupValueId == id){

        return item.lookupValueName;
      }

    })

  }

  showConfirmModal(index) {
    this.modalService.showConfirmModal(index);
  }

  showUserInfo(){
    this.outputParams.emit(true);
  }

  getUserDetails(userId){

    this.userService.getAllApprovedUsers().subscribe((res : any) => {

      var approvedUsersData = res.filter( item => {
        if(item.id == userId)
          return item;
      });

      this.user = approvedUsersData[0];
      let getApprovedUserParam ={
        userId:userId,
        apartmentId:parseInt(this.cookieService.get('apartmentId'))
      }
      this.apartmentService.getApartmentBlockUnitByUserId(getApprovedUserParam).subscribe((res : any) => {
        this.unit = res[0];
        this.apartmentBlockUnitId = this.unit.apartmentBlockUnitId;

        this.vehicleService.getVehicleByVehicleId(this.apartmentBlockUnitId).subscribe((res : any) => {

          this.vehicle = res[0] || {};
          this.isUserDataLoaded = true;

        });

        

      });

    },
    error => {
      console.log(error);
    }
    );

   

  }

  getApartmentUnit(userId){
    let getAllApartmentParam ={
      userId:userId
    }
    this.apartmentService.getAllApartmentBlockUnitUsersByUserId(getAllApartmentParam).subscribe((unit:any) => {
      this.unitListData = unit;
      console.log('res',unit);
      _.each(this.unitListData, item => {
        let getBlockUnitId = {
          apartmentBlockUnitId:item.apartmentBlockUnitId
        }
        this.apartmentService.getApartmentBlockUnitById(getBlockUnitId).subscribe((data:any) => {
          this.unitNameList.push(data[0]);
          this.isUserDataLoaded = true;
        },
        error => {

        });

      })
    },
    error => {
      
    }); 
  }


  ngOnInit() {

    this.user = {};
    this.unit = {};
    this.vehicle = {};

    if(this.route.params['value'].id != undefined){
      this.isSeperatePage = true;
      let petParams = {
        LookupTypeId: 85
      }
      this.lookupService.getLookupValueByLookupTypeId(petParams).subscribe((res:any) => {
        this.petTypeList = res;
      });
  
      this.getUserDetails(this.route.params['value'].id);
      this.getApartmentUnit(this.route.params['value'].id);
    }

    let petParams = {
      LookupTypeId: 85
    }
    this.lookupService.getLookupValueByLookupTypeId(petParams).subscribe((res:any) => {
      this.petTypeList = res;
    });

    // delete user
    this.sharedService.unitlistdeleteindexcast.subscribe( index => {

      if(index != null){

        var params = {
          userId: index,
          updateUserId: parseInt(this.cookieService.get('userId'))
        }

        this.isUserDataLoaded = false;

        this.userService.deleteUserById(params).subscribe((res:any) => {

          
          this.isUserDataLoaded = true;
          this.sharedService.setUnitListDeleteIndex(null);

        }, error => {
          console.log(error);
        });

        
      }

    });


  }

  ngOnChanges(changes: SimpleChanges) {

    //user details
    if(this.selectedTab != -1){

      this.isUserDataLoaded = false;

      this.getUserDetails(this.selectedTab);
      this.getApartmentUnit(this.selectedTab);
    }

    let params = {
      LookupTypeId: 2
    }

    this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res:any) => {

      this.vehicleTypeData = res;

    },
    error => {
      
    }
    );

    let getFamilyParam ={
      apartmentBlockUnitUserId:this.selectedTab
    }

    this.userService.getUserFamilyMembersByUserId(getFamilyParam).subscribe((res:any) => {

  		this.familyDataList = res.filter(item => { return item.isActive });;

    })
    
    let getOutOfCondoParams = {
      "apartmentId": parseInt(this.cookieService.get('apartmentId')),
      "ApartmentBlockUnitUserId": this.apartmentBlockUnitId
  }
  this.apartmentService.getOutofCondobyApartmentblockunituser(getOutOfCondoParams).subscribe((unit:any) => {
  //   this.historyListData = unit.filter(data => {
  //     data.outFrom = moment(data.outFrom).format('MM/DD/YYYY');
  //   data.outTo = moment(data.outTo).format('MM/DD/YYYY');
  //   data.startDate = moment(data.outFrom).format('MM/DD/YYYY');
  //   data.expiryDate = moment(data.outTo).format('MM/DD/YYYY');
  //   return data.isActive;
  // })
  // let length = this.historyListData.length - 1;
  //       this.data = this.historyListData[length];
  //       this.isDataLoaded = true;
    },
    error => {
      
    });
    

  }

}
